import config from "../../components/config/config.vue"
export default {
	data() {
		return {
							userList: [],
							/* 选择的用户下标 */
							pickerUserIndex: -1,
							value:""
		}
	},
	methods: {
		
		/* 列表触摸事件 */
		listTap: function(e,val){
			// var us=e.currentTarget.dataset.index
			uni.showActionSheet({
			    itemList: ['进入聊天', '取消关注'],
			    success: function (res) {
			        //console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					if(res.tapIndex==0){
						var value = uni.getStorageSync('token');
						uni.request({
						    url: config.httpUrl +'/api/v1/user/chatlist',
							method:"POST",
							data: {
								'rid':parseInt(val["id"]),
							},
						    header: {
						       'content-type': 'application/x-www-form-urlencoded',
							   'Authorization' : value,
						    },
						    success: (res) => {
								var jsondata=res.data
								if(jsondata.code=='0'){
									uni.navigateTo({
									url: 'chat?id='+val["id"]+'&name='+val["name"]
									});																			
								}else{
									 config.txtshow(jsondata.msg)				  				
								}								
						    },
							fail: () =>{  
								 config.txtshow('网络异常')
						     }, 
						});
						
						 
					}else if(res.tapIndex==1){
						var value = uni.getStorageSync('token');
						if(value){
						  	uni.request({
						  	    url: config.httpUrl +'/api/v1/user/friend',
						  		method:"DELETE",
								data:{
									"oid":val["id"]
								},
						  	    header: {
						  		   'Authorization' : value,
						  	    },
						  	    success: (res) => {
						  			var jsondata=res.data
						  			if(jsondata.code=='0'){								
										config.txtshow("删除成功")
										uni.startPullDownRefresh();
						  			}else{
						  				 config.txtshow(jsondata.msg)				  				
						  			}								
						  	    },
						  		fail: () =>{  
						  			 config.txtshow('网络异常')
						  	     }, 
						  	});
						  }else{
						  	//跳转到登录界面
							console.log("需要登录")
						  	uni.redirectTo({
						  		url: '../user/login'
						  	});
						    
						}
					}else{
						//其他行为
					}
			    },
			    fail: function (res) {
					//不进行任何操作
			        // console.log(res.errMsg);
			    }
			});				

			
			
		},
		/* 获取列表数据 */
		getListData() {
			let list = [];
			//向后端发起请求
			if(this.value){
			  	uni.request({
			  	    url: config.httpUrl +'/api/v1/user/friendlist',
			  		method:"GET",
			  	    header: {
			  		   'Authorization' : this.value,
			  	    },
			  	    success: (res) => {
			  			var jsondata=res.data
			  			if(jsondata.code=='0'){								
							for (let i = 0; i < jsondata.data.length; i++) {								
								//头像
								var photo="../../static/my/unlogin.jpg"
								if(jsondata.data[i]["avatar"]){
									photo=jsondata.data[i]["avatar"]
								}
																										
								list.push({
									"id":jsondata.data[i]["id"],
									"name":jsondata.data[i]["nickname"],
									"avatar":photo
									})
								}
							this.userList = list;								
			  			}else{
			  				 config.txtshow(jsondata.msg)				  				
			  			}								
			  	    },
			  		fail: () =>{  
			  			 config.txtshow('网络异常')
			  	     }, 
			  	});
			  }else{
			  	//跳转到登录界面
			  	uni.redirectTo({
			      url: 'login'
			  });
			    console.log("需要登录")
			}
			
			
		},
	},
	onLoad() {
		this.value = uni.getStorageSync('token');
		this.getListData();	
	
	},
	onTabItemTap(){
		uni.startPullDownRefresh();
	},
	onPullDownRefresh() {
			this.getListData();	
	        setTimeout(function () {
	            uni.stopPullDownRefresh();
	        }, 1000);
	    }
}
